.cart-wrap {
    margin: 78px 0 80px;
    h3 {
        margin-bottom: 60px;
        font-size: 24px;
    }
    .cart-step {
        float: right;
        margin-top: -85px;
        span {
            position: relative;
            display: inline-block;
            padding: 0 20px;
            font-size: 14px;
            color: #898989;
            &.last {
                padding-right: 0;
                &::after {
                    display: none;
                }
            }
            &::after {
                content: '';
                position: absolute;
                right: -4px;
                top: -1px;
                width: 8px;
                height: 15px;
                background: url('/_ui/img/pc/icn/icn-cart-step.png') no-repeat;
                opacity: 0.3;
            }
            &.active {
                color: #1f1f1f;
                &::after {
                    opacity: 1;
                }
            }
        }
    }
    .order-lists {
        .head {
            .tit-1 {
                width: 486px;
            }
            .tit-2,
            .tit-3,
            .tit-5 {
                text-align: center;
                width: 140px;
            }
            .tit-3 {
                width: 150px;
            }
            .tit-4 {
                width: 160px;
            }
            .tit-6 {
                width: 84px;
            }
        }
        .list {
            .range,
            .division {
                width: 140px;
                .company {
                    margin-top: 0;
                    line-height: 1.2;
                    font-size: 13px;
                    color: #1f1f1f;
                    font-weight: normal;
                    max-height: 35px;
                    word-wrap: break-word;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    text-decoration: underline;
                }
                .date {
                    font-size: 13px;
                    color: #898989;
                    line-height: 1.2;
                }
            }
            .price {
                line-height: 1.6;
                del {
                    display: block;
                    color: #aaa;
                }
                .family {
                    display: block;
                    color: #f23d4e;
                }
                .btn {
                    display: table;
                    margin: 0 auto;
                    height: 26px;
                    line-height: 26px;
                    padding: 0 10px;
                }
                .family-sale-off {
                    background-color: #bbb;
                }
                .family-sale-on {
                    background-color: #f23d4e;
                }
            }
            .size {
                padding: 60px 0;
            }
            .delivery {
                width: 160px;
            }
            .delivery.pickup {
                padding: 0 20px;
            }
            .state {
                .soldout {
                    font-size: 12px;
                    color: #f23d4e;
                }
            }
        }
        .set-list {
            padding-left: 50px;
        }
        .set-main {
            padding-left: 0;
        }
        .set-list.set-prod {
            padding-left: 80px;
        }
    }
    .shipping {
        padding: 40px 0 65px;
        font-size: 14px;
        border-top: 4px solid #000;
        h4 {
            font-weight: bold;
            margin-top: 1px;
            margin-bottom: 17px;
            float: left;
            margin-right: 10px;
            small {
                color: #aaa;
            }
        }
        small {
            color: #aaa;
            line-height: 1.2571;
            font-size: 13px;
        }
        dl {
            color: #898989;
            font-size: 14px;
            line-height: 1.8571;
            dt,
            dd {
                float: left;
                margin-bottom: 20px;
            }
            dt {
                clear: left;
                width: 130px;
            }
            dd {
                float: left;
                em {
                    color: #1f1f1f;
                }
            }
            &::after {
                content: '';
                display: block;
                clear: both;
            }
        }
        &::after {
            content: "";
            display: block;
            position:absolute;
            top: -40px;
            right: 0;
            left: 0;
            height: 4px;
            background: #000;
        }
        .note {
            font-size: 13px;
            margin-top: 40px;
            padding-top: 15px;
            border-top: 1px solid #f1f1f1;
            h5 {
                margin: 25px 0 18px;
            }
            li {
                color: #aaa;
                line-height: 1.6923;
                position: relative;
                padding: 0 0 10px 10px;
                &::before {
                    content: '';
                    position: absolute;
                    top: 8px;
                    left: 0;
                    width: 2px;
                    height: 2px;
                    background-color: #c1c1c1;
                }
            }
        }
        .gifts {
            margin-left: -24px;
            margin-top: 30px;
            .gift-title {
                font-size: 14px;
                margin-left: 24px;
            }
            li {
                float: left;
                width: 588px;
                margin-left: 24px;
                .gift {
                    color: #1f1f1f;
                    margin-top: 20px;
                    padding: 0 16px;
                    background-color: #f7f7f7;
                    .thumb {
                        vertical-align: middle;
                        width: 100px;
                        min-height: 100px;
                        margin: 10px 10px 10px 0;
                        display: inline-flex;
                        display: -webkit-inline-flex;
                        display: -ms-inline-flex;
                        justify-content: center;
                        -webkit-justify-content: center;
                        align-items: center;
                        -webkit-align-content: center;
                        img {
                            width: 100%;
                        }
                    }
                    .cnt {
                        vertical-align: middle;
                        display: inline-block;
                        max-width: 380px;
                        .brand {
                            font-size: 11px;
                            font-weight: bold;
                            margin-bottom: 10px;
                        }
                        .title {
                            color: #898989;
                            font-size: 15px;
                            margin-bottom: 0;
                        }
                    }
                }
            }
            &::after {
                content: '';
                display: block;
                clear: both;
            }
        }
        .bul-list {
            margin-top: 40px;
        }
    }
    .all-select {
        margin-top: 40px;
        margin-bottom: 30px;
        .checkbox {
            margin-right: 15px;
        }
    }
    .bul-list {
        li {
            position: relative;
            margin-top: 12px;
            padding-left: 10px;
            font-size: 13px;
            color: #aaa;
            &:first-child {
                margin-top: 0;
            }
            &::before {
                content: '';
                position: absolute;
                left: 0;
                top: 5px;
                width: 2px;
                height: 2px;
                background-color: #aaa;
            }
        }
    }
    .cart-info {
        margin-top: 10px;
        padding-top: 40px;
        border-top: 1px solid #f1f1f1;
        h4 {
            font-size: 14px;
            color: #1f1f1f;
        }
        ul {
            margin-top: 20px;
        }
    }
    .billing {
        display: table;
        position: relative;
        width: 100%;
        //margin-top: 80px;
        padding: 75px 0;
        //border-top: 1px solid #f1f1f1;
        background-color: #fafafa;
        & > * {
            display: table-cell;
            text-align: center;
            vertical-align: middle;
            font-size: 18px;
            color: #1f1f1f;
        }
        &::after {
            content: "";
            display: block;
            position:absolute;
            top: -40px;
            right: 0;
            left: 0;
            height: 4px;
            //background: #000;
        }
        strong {
            display: inline-block;
            min-width: 115px;
            font-weight: normal;
            text-align: right;
        }
        span {
            display: inline-block;
            margin-left: 15px;
            text-align: right;
        }
        .default-pay {
            div {
                margin: 10px 0;
            }
        }
        .sale-pay {
            div {
                margin: 18px 0;
            }
            .before {
                position: relative;
                &::before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: 50%;
                    width: 18px;
                    height: 3px;
                    margin-top: -2px;
                    background-color: #1f1f1f;
                }
                div:last-child {
                    margin-top: 15px;
                }
                strong {
                    text-align: left;
                }
                .small {
                    margin: 15px 0;
                    font-size: 14px;
                    small {
                        display: inline-block;
                        color: #898989;
                        text-align: left;
                        min-width: 115px;
                    }
                }
            }
        }
        .total {
            div {
                position: relative;
                font-size: 20px;
                em {
                    color: #f23d4e;
                }
                &::before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: 50%;
                    width: 18px;
                    height: 3px;
                    margin-top: 1px;
                    background-color: #1f1f1f;
                }
                &::after {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: 50%;
                    width: 18px;
                    height: 3px;
                    margin-top: -6px;
                    background-color: #1f1f1f;
                }
            }
        }
    }
    .end-row {
        padding-top: 0;
        border: 0;
        .btn-order {
            background-color: #f23d4e;
        }
    }
    .no-result {
        margin: 150px 0;
        text-align: center;
        font-size: 16px;
        color: #898989;
    }
}
.layer-popup {
    .layer {
        .head {
            .title {
                .tit_1 {
                    width: 475px;
                }
                .tit_2 {
                    text-align: center;
                    width: 105px;
                }
                .tit_3 {
                    text-align: center;
                    width: 200px;
                }
            }
        }
        .content {
            .product {
                .size {
                    text-align: center;
                }
            }
        }
    }
}
.layer-size {
    .layer {
        .content {
            dl {
                padding: 60px 0 70px;
                text-align: center;
                dt {
                    display: inline-block;
                    margin-right: 25px;
                    font-size: 14px;
                    color: #898989;
                }
                dd {
                    display: inline-block;
                    width: 122px;
                    .select {
                        position: relative;
                        top: -9px;
                        width: 100%;
                    }
                }
            }
        }
    }
}
.popup-wow-guide {
    position: fixed;
    top: 20%;
    right: 20%;
    width: 450px;
    background-color: #000;
    z-index: 1000;
    a,
    img {
        display: block;
    }
    .wow-close-btn {
        position: absolute;
        right: 23px;
        top: 23px;
        width: 27px;
        height: 27px;
        background: url(/_ui/img/pc/btn-pop-cls.png) no-repeat;
        background-size: 100%;
    }
    .wow-cookie-btn {
        display: block;
        padding: 10px;
        background-color: #000;
    }
}